/* Stripping Styles */

html {
    font-size: 62.5%;
  }
  
  h1,
  h2,
  h3,
  h4,
  p,
  ul,
  li {
    margin: 0;
    padding: 0;
  }
  
  .wrapper {
    max-width: 1200px;
    width: 80%;
    margin: 0 auto;
  }
  
  ul {
    list-style: none;
  }
  
  img {
    max-width: 100%;
    height: auto;
    display: block;
  }
  
  button {
    font-family: 'Courier Prime', monospace;
    border: none;
    cursor: pointer;
    padding: 10px 40px;
    font-size: 2.5rem;
    border-radius: 10px;
    color: white;
    background-color: #222222;
    border: 2px solid white;
    // width: 100%;
    // text-align: center;
  }
  
  button:hover {
    background-color: darkgrey;
  }
  
  button:focus {
    border: 2px solid #857baf;
  }
  
  a {
    display: inline-block;
    text-decoration: none;
    color: inherit;
  }
  
  a:focus {
    text-decoration: underline;
  }
  
  /* General */
  
  body {
    font-family: 'Courier Prime', monospace;
    color: white;
  }
  
  h1 {
    font-family: 'Rowdies', cursive;
    font-size: 8rem;
    color: white;
    text-transform: uppercase;
  }
  
  h2 {
    font-size: 3.2rem;
    color: white;
  }
  
  h3 {
    font-size: 3rem;
    color:#222222;
  }
  
  h4 {
    font-family: 'Roboto', sans-serif;
    font-size: 2.5rem;
    color: white;
  }
  
  p {
    font-size: 2rem;
  }
  
  .app {
    background: url(../assets/blackbgwithsmoke.jpg);
    background-size: cover;
  }